import url from '@/request/url.js'
import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			diaries: []
		}
	},
	computed: {
		...mapState({
			userInfo: state => state.userInfo
		})
	},
	methods: {
		del(id) {
			uni.showModal({
				title: '确定要删除这条动态码？',
				success: res => {
					if (res.confirm) {
						uni.request({
							url: url.delDiary + id,
							method: "DELETE",
							success: resp => {
								console.log(resp.data);
								uni.showToast({
									title: '删除成功！',
									icon: "success"
								})
								this.getAllDiary();
							}
						})
					}
				}
			})
		},
		preview(index, imgs) {
			uni.previewImage({
				urls: imgs,
				current: index
			});
		},
		getAllDiary() {
			uni.request({
				url: url.getAllDiary,
				data: {
					uid: this.userInfo.id
				},
				success: resp => {
					console.log(resp.data);
					this.diaries = resp.data
				}
			})
		}
	},
	onShow() {
		this.getAllDiary();
	}
}
